import styled from 'styled-components'
import {useState,useEffect} from 'react'
const Warpper = styled.div`
width:${props=>props.width}%;
max-width:100%;
height:10px;
background-color: #5577FD;
`
export default function TimeSlider({startTimestamp,nowTimestamp,endTimestamp }) {
  const width = parseInt(((nowTimestamp-startTimestamp) / (endTimestamp-startTimestamp))*100)
  return (
    <div className='time-slider'>
      <Warpper width={width}>

      </Warpper>
    </div>

  )
}