import React, { useState ,useEffect} from 'react';
import Button from "../Button/Button";
import styled from 'styled-components';

const Wrapper = styled.div`
    display : flex;
    width: 100%;
    justify-content: space-between;
  `
export default function ButtonGroup({groups = ['25%','50%','75%','100%'],selectedBorderColor,defaultBorderColor ,onChange,defaultValue ,outline,outlineColor,width,height,bgColor,className}){
  const [value, setValue] = useState(defaultValue)
  const select = current => {
    if(current !== value) {
      setValue(current)
      onChange && onChange(current);
    } else if(current === value){
      setValue('')
      onChange && onChange('');
    } else {
      setValue(current)
    }
  }
  useEffect(() => {
    setValue(defaultValue)
  }, [defaultValue])
  return (
  <Wrapper className={className}>
    {groups.map((text,index) => (
      <Button label={text} key={index} selectedBorderColor ={selectedBorderColor} isSelected={text === value} disabled={false} outline = {outline} bgColor={bgColor} className='g-button' defaultBorderColor={defaultBorderColor} outlineColor={outlineColor} width={width} height={height} onClick={() => select(text)}/>
    ))}
  </Wrapper>)
}