import React, { useState, useEffect } from 'react'
import Button from '../../components/Button/Button'
import Slider from '../../components/Slider/TimeSlider';
import useConfig from "../../hooks/useConfig";
import { useAlert } from 'react-alert'
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import apiProxy from '../../model/ApiProxy';
import { eqInNumber } from '../../utils/utils';
import './claim.less'
export default function Claim({ lang, poolShow = true, wallet={} }) {
  const [remainingTime, setRemainingTime] = useState('')
  const [startTimestamp, setStartTimestamp] = useState(0)
  const alert = useAlert();
  const [endTimestamp, setEndTimestamp] = useState(0)
  const [nowTimestamp, setNowTimestamp] = useState(0)
  const [claimInfo, setClaimInfo] = useState({})
  const [claimed, setClaimed] = useState(false);
  const [firstTime, setFirstTime] = useState('')
  const config = useConfig(claimInfo.chainId) 
  useEffect(() => {
    let interval = null;
    //计数器
    interval = window.setInterval(() => {
      let period = 3600 * 8;
      let current = parseInt(Date.now() / 1000);
      let epochBegin = parseInt(current / period) * period;
      let dis = (epochBegin + period - current);
      let h = parseInt(dis / 3600);
      let m = parseInt((dis % 3600) / 60)
      let s = parseInt(dis % 60)
      setStartTimestamp(epochBegin)
      setEndTimestamp(epochBegin + period)
      setNowTimestamp(current)
      setRemainingTime(`${h} ${lang['h']} ${m} ${lang['m']} ${s} ${lang['s']}`);
    }, 1000);
    return () => {
      interval && clearInterval(interval);
    };

  }, []);
  const loadClaimInfo = async () => {
    if (wallet.isConnected()) {
      const claimInfo = await apiProxy.request('getUserInfoAll', [wallet.detail.account])
      const claimed = (+claimInfo.total).toFixed(2);
      const unclaimed = claimInfo.valid ? (+claimInfo.amount).toFixed(2) : 0;
      const harvestDeriLp = claimInfo.lp;
      const harvestDeriTrade = (+claimInfo.trade).toFixed(2);
      const chainId = claimInfo.chainId
      setClaimInfo({ claimed, unclaimed, harvestDeriLp, harvestDeriTrade, chainId })
    }
  }

  const claimDeri = async ()=>{
    if(config.name === undefined) {
			alert.error(`${lang['wrong-network']}`);
			return;
		}
		if(!eqInNumber(wallet.detail.chainId,claimInfo.chainId)) {
			alert.error(`${lang['your-deri-is-on']} ${ config.name } ${lang['connect-to']} ${ config.name } ${lang['to-claim']}`)
			return ;
		}
		if ((+claimInfo.unclaimed) === 0) {
			alert.error(lang['no-deri-to-claim-yet']);
			return;
		}
		let now = parseInt(Date.now() / 1000) % (3600 * 8);
		if (now < 1800) {
			alert.error(lang['claiming-DERI-is-disabled-during-first-30-minutes-of-each-epoch']);
			return;
    }
    let res = await apiProxy.request('mintDToken',[wallet.detail.chainId,wallet.detail.account],{ includeResponse: true })
		if(res.success){
			return true;
		} else {
			alert.error(lang['claim-failed'])
			return false;
		}
    
  }
  const click = async () => {
		if(wallet.isConnected()){
			const res = await claimDeri();
			if(res.success){
        setClaimed(true)
			}
		} else {
			try{
				const result =await wallet.connect();
				return result ? true : false
			} catch (e){
				return false
			}
		} 
	}

  const getMonth = (month) => {
    let monthText;
    switch (month) {
      case 1:
        monthText = lang['jan']
        break;
      case 2:
        monthText = lang['feb']
        break;
      case 3:
        monthText = lang['mar']
        break;
      case 4:
        monthText = lang['apr']
        break;
      case 5:
        monthText = lang['may']
        break;
      case 6:
        monthText = lang['jun']
        break;
      case 7:
        monthText = lang['jul']
        break;
      case 8:
        monthText = lang['aug']
        break;
      case 9:
        monthText = lang['sep']
        break;
      case 10:
        monthText = lang['oct']
        break;
      case 11:
        monthText = lang['nov']
        break;
      case 12:
        monthText = lang['dec']
        break;
      default:
        break;
    }
    return monthText
  }

  const getUserFirstClaimInfo = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getUserFirstClaimInfo', [wallet.detail.account])
      if (res.first_timestamp) {
        let date = new Date(res.first_timestamp*1000)
        let year = date.getFullYear()
        let month = date.getMonth() + 1;
        month = getMonth(month)
        let day = date.getDate();
        setFirstTime(`${month} ${day}, ${year}` )
      }
    }
  }
  useEffect(() => {
    let interval = window.setInterval(loadClaimInfo, 1000 * 60 * 3);
    loadClaimInfo();
    getUserFirstClaimInfo();
    return () => clearInterval(interval);
  }, [wallet.detail.account])
  return (
    <div className='mining-claim'>
      <div className='mining-claim-info'>
        <div className='deri-num-slider'>
          <div className='deri-claim-time'>
          {lang['current-epoch-remaining-time']} : {remainingTime}
          </div>
          {claimInfo.harvestDeriLp > 0 && <div className='deri-claim-text'>
            {lang['your-harvest-in-current']}  <DeriNumberFormat value={claimInfo.harvestDeriLp} decimalScale={2} displayType='text' di thousandSeparator={true} />  $DERI.
          </div>}
          {poolShow && <div className='deri-claim-text'>
            You have <DeriNumberFormat value={claimed ? 0 : claimInfo.unclaimed} displayType='text' thousandSeparator={true} /> unclaimed $DERI across all staked pools. You’ve earned a total of <DeriNumberFormat value={claimed ? (+claimInfo.claimed) + (+claimInfo.unclaimed) : claimInfo.claimed} displayType='text' thousandSeparator={true} /> Deri token 
            {firstTime && <span> since {firstTime}. Woohoo!</span>}
          </div>} 
          <div className='deri-claim-slider'>
            <Slider startTimestamp={startTimestamp} nowTimestamp={nowTimestamp} endTimestamp={endTimestamp} />
          </div>
        </div>
        {poolShow && <div className='cliam-button'>
          <Button fontWeight='bold' label={lang['claim-all']} onClick={click} />
        </div>}
      </div>
    </div>
  )
}