import styled from 'styled-components'
import React, { useState } from 'react';
const Wrapper = styled.div`
    cursor: pointer;
    width:${props => props.width}px;
    height:${props => props.height}px;
    display:flex;
    .tab-left.inactive{
      border-radius:0px 0px 4px 0px;
    }
    .tab-right.inactive{
      border-radius:0px 0px 0px 4px;
    }
    .tab-left.active{
      border-radius:0px 4px 0px 0px;
      ${props => props.borderR};
      height:14px;
    }
    .tab-right.active{
      border-radius:4px 0px 0px 0px;
      ${props => props.borderL};
      height:14px;
    }
    .inactive,.active{
      width:50%;
      box-sizing: content-box;
      padding: 15px 24px;
      font-size:${props => props.size}px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .inactive{
      background:${props => props.inactiveBgColor};
      ${props => props.borderB}
    }
    .active {
      color: ${props => props.activeFontColor};
      background : ${props => props.activeBgColor};
      ${props => props.borderT};
    }
    .icon-tab{
      margin-right:20px;
      width:25px;
      height:25px;
    }
  `
export default function Tab({ tabSet, inactiveBgColor,activeBgColor,icon, className, width = 238, height = 47, size = 16,borderTop = false,borderBottom = false, divider = false }) {
  const arr = Object.keys(tabSet)
  const [selected, setSelected] = useState(arr[0])
  const styles = tabSet[selected].style
  const borderT = borderTop ? `border-top:1px solid ${tabSet[selected].borderColor}`:"border-top:0"
  const borderB = borderBottom ? `border-bottom:1px solid ${tabSet[selected].borderColor}`:"border-bottom:0"
  const borderL = divider ? `border-left:1px solid ${tabSet[selected].borderColor}`:"border-left:0"
  const borderR = divider ? `border-right:1px solid ${tabSet[selected].borderColor}`:"border-right:0"
  const activeFontColor = tabSet[selected].fontColor ? tabSet[selected].fontColor : '#FFFFFF'

  function select(item) {
    setSelected(item)
  }

  return (
    <div style={styles} className={className}>
      <Wrapper className='tab-header' width={width} height={height} 
        activeFontColor={activeFontColor} activeBgColor ={activeBgColor} inactiveBgColor={inactiveBgColor} size={size} borderT={borderT} borderB={borderB} borderL={borderL} borderR={borderR}>
        {arr.map(item => (
          <div
            key={item}
            className={item === selected ? `${tabSet[item].direction} active` : `${tabSet[item].direction} inactive`}
            onClick={() => select(item)}>
            {tabSet[item].icon ? <img alt='' className='icon-tab' src={item === selected ? tabSet[item].icon :tabSet[item].inicon } /> :""}  {item}
          </div>
        ))}
      </Wrapper>
      {tabSet[selected].content}
    </div>
  )
}