/* eslint-disable react/no-danger-with-children */
import styled from 'styled-components';
import Tooltip from '../Tooltip/Tooltip';
import { MD5, isMobile } from '../../utils/utils';


const Wrapper = styled.div`
  .text {
    border-bottom : 1px dashed #93A1B8;
    width : max-content;
    cursor : pointer;
  }
`


export default function UnderlineText({text,id = MD5(text) ,tip,multiline,className,width = 'auto',children}){
  const calculatePosition = (position,currentEvent,currentTarget,refNode,place,desiredPlace,effect,offset) => {
    const rect = currentTarget.getBoundingClientRect();
    const tooltipNodeRect = refNode.getBoundingClientRect();
    //位置被遮盖
    let {x : left ,y :top,height} = rect
    top = top + height
    if(left + tooltipNodeRect.width > document.documentElement.clientWidth) {
      left = left - tooltipNodeRect.width  + rect.width ;
    }
    if(top + tooltipNodeRect.height >= document.documentElement.clientHeight) {
      top = top  - tooltipNodeRect.height + height /2
    }
    if(left < 0){
      left = 0
    }
    if(top < 0) {
      top = 0
    }
    return {top:top ,left : left}
  }
  return(
    <Wrapper className={className}>
      <div className='text' data-for={id} data-tip={tip} data-event={isMobile() ? 'click' : undefined}>
        {text || children}
      </div>
      {tip &&<Tooltip id={id} width={width} padding='12' place='bottom' overridePosition={calculatePosition} borderRadius={8} multiline={multiline}>
      </Tooltip>}
    </Wrapper>
  )
}